
        // {
        //   id: 1,
        //   title: 'event1',
        //   start: moment().hours(12).minutes(0),
        //   cssClass: 'common',
        //   YOUR_DATA: {
        //     dateAlarm: moment().hours(12).minutes(0),
        //   },
        // },
        // {
        //   id: 2,
        //   title: 'event2',
        //   start: moment().add(-1, 'days'),
        //   end: moment().add(1, 'days'),
        //   cssClass: 'common',
        //   allDay: true,
        // },


<template lang="html">
  <div class="formCalendar">
    <h2 class=description>Cadastro de novo evento</h2>
    <div class="inputBox">
      <md-input-container>
        <label>Nome</label>
        <md-input type="text" v-model="event.title" maxlength="20"></md-input>
      </md-input-container>
      <md-input-container>
        <label>Local</label>
        <md-input type="text" v-model="event.location" maxlength="20"></md-input>
      </md-input-container>
      <md-input-container>
        <label>Hora do alarme</label>
        <md-input type="text" v-model="event.alarm" maxlength="20"></md-input>
      </md-input-container>
      <md-input-container>
        <label>Horário de inicio</label>
        <md-input type="text" v-model="event.start" maxlength="20"></md-input>
      </md-input-container>
      <md-input-container>
        <label>Horário de fim</label>
        <md-input type="text" v-model="event.end" maxlength="20"></md-input>
      </md-input-container>
      <md-switch v-model="event.allDay" id="switch" name="switch" class="md-warn">O dia todo?</md-switch>
    </div>
    <md-button class="button-send md-raised md-dense" type="button" name="button" @click="addEvent">
      <router-link to="/events">Enviar</router-link></md-button>
  </div>
</template>

<script>
import EventBus from './EventBus';

export default {
  name: 'InputCalendar',
  methods: {
    addEvent() {
      const obj = { events: this.listOfEvents };
      this.listOfEvents.push(this.event);
      localStorage.setItem('events', JSON.stringify(obj));
      EventBus.$emit('addEvent', this.event);
      this.event = {};
    },
  },
  mounted() {
    this.listOfEvents = JSON.parse(localStorage.getItem('events') || {}).events || [];
  },
  data() {
    return {
      event: {
      },
      listOfEvents: [],
    };
  },
};
</script>

<style lang="css">

.button-send {
  width: 10%;
}

.formCalendar {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
}

.inputBox {
  width: 80%;
  justify-content: center;
  flex-flow: column wrap;
}

.description {
  color: #A9A9A9;
  margin: 5%;
  font-family: sans-serif;
}

</style>


